// Contact Management API v1
// GET /api/v1/contacts - List contacts with pagination, search, filter
// POST /api/v1/contacts - Create new contact

import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'

// GET - List contacts
export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    
    // Query parameters
    const subAccountId = searchParams.get('subAccountId')
    const search = searchParams.get('search') || ''
    const status = searchParams.get('status') || ''
    const tags = searchParams.get('tags')?.split(',').filter(Boolean) || []
    const page = parseInt(searchParams.get('page') || '1')
    const limit = parseInt(searchParams.get('limit') || '50')
    const sortBy = searchParams.get('sortBy') || 'createdAt'
    const sortOrder = searchParams.get('sortOrder') || 'desc'
    
    // Validation
    if (!subAccountId) {
      return NextResponse.json(
        { success: false, error: 'subAccountId is required' },
        { status: 400 }
      )
    }
    
    // Build query
    let query = supabaseAdmin
      .from('Contact')
      .select('*', { count: 'exact' })
      .eq('subAccountId', subAccountId)
    
    // Apply search filter
    if (search) {
      query = query.or(`firstName.ilike.%${search}%,lastName.ilike.%${search}%,email.ilike.%${search}%,company.ilike.%${search}%`)
    }
    
    // Apply status filter
    if (status) {
      query = query.eq('status', status)
    }
    
    // Apply tags filter (contains any of the tags)
    if (tags.length > 0) {
      query = query.overlaps('tags', tags)
    }
    
    // Apply sorting
    query = query.order(sortBy, { ascending: sortOrder === 'asc' })
    
    // Apply pagination
    const offset = (page - 1) * limit
    query = query.range(offset, offset + limit - 1)
    
    const { data: contacts, error, count } = await query
    
    if (error) throw error
    
    return NextResponse.json({
      success: true,
      data: {
        contacts: contacts || [],
        pagination: {
          page,
          limit,
          total: count || 0,
          totalPages: Math.ceil((count || 0) / limit),
        },
      },
    })
  } catch (error: any) {
    console.error('GET /api/v1/contacts error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to fetch contacts' },
      { status: 500 }
    )
  }
}

// POST - Create new contact
export async function POST(request: NextRequest) {
  try {
    const body = await request.json()
    const {
      subAccountId,
      firstName,
      lastName,
      email,
      phone,
      company,
      jobTitle,
      website,
      address,
      city,
      state,
      zipCode,
      country,
      language,
      timezone,
      tags,
      customFields,
      status,
      source,
      notes,
    } = body
    
    // Validation
    if (!subAccountId || !firstName || !lastName || !email) {
      return NextResponse.json(
        {
          success: false,
          error: 'subAccountId, firstName, lastName, and email are required',
        },
        { status: 400 }
      )
    }
    
    // Validate email format
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
    if (!emailRegex.test(email)) {
      return NextResponse.json(
        { success: false, error: 'Invalid email format' },
        { status: 400 }
      )
    }
    
    // Check if contact with same email already exists in this sub-account
    const { data: existingContact } = await supabaseAdmin
      .from('Contact')
      .select('id')
      .eq('subAccountId', subAccountId)
      .eq('email', email)
      .single()
    
    if (existingContact) {
      return NextResponse.json(
        { success: false, error: 'Contact with this email already exists' },
        { status: 409 }
      )
    }
    
    // Build insert data (conditionally include customFields if column exists)
    const insertData: any = {
      subAccountId,
      firstName,
      lastName,
      email,
      phone: phone || null,
      company: company || null,
      jobTitle: jobTitle || null,
      website: website || null,
      address: address || null,
      city: city || null,
      state: state || null,
      zipCode: zipCode || null,
      country: country || null,
      language: language || 'en',
      timezone: timezone || null,
      tags: tags || [],
      status: status || 'new',
      source: source || null,
      notes: notes || null,
      score: 0,
      lastActivityAt: new Date().toISOString(),
    }
    
    // Only add customFields if provided (will be ignored if column doesn't exist)
    if (customFields && Object.keys(customFields).length > 0) {
      insertData.customFields = customFields
    }
    
    // Create contact
    const { data: contact, error } = await supabaseAdmin
      .from('Contact')
      .insert(insertData)
      .select()
      .single()
    
    if (error) throw error
    
    return NextResponse.json({
      success: true,
      data: { contact },
    })
  } catch (error: any) {
    console.error('POST /api/v1/contacts error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to create contact' },
      { status: 500 }
    )
  }
}
